@mixin at-least($device-width) {
  @media screen and (min-width: $device-width) {
    @content 
  }
}

@mixin until($device-width) {
  @media screen and (max-width: $device-width - 1) {
    @content 
  }
}
%clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}